import React, { Component } from 'react';
import * as tf from '@tensorflow/tfjs';
import dl from '../Data-traveketquathembangai/DulieuHuanLuyen.json';
class Viewdata extends Component {


    constructor(props) {
        super(props);
        this.state = {
            data: this.props.dulieuchungminhnhdandan,
            ketquachinhthuc: ''
        };
    }
    showconten = () => {
        if (this.props.dulieuchungminhnhdandan.nationality === "N/A") {
            return (
                <h4>HÃ¬nh áº£nh Ä‘áº§u vÃ o : chá»©ng minh nhÃ¢n dÃ¢n (Loáº¡i cÅ©) </h4>

            )
        }
        else if (this.props.dulieuchungminhnhdandan.nationality === undefined) {
            return (
                <h4>HÃ¬nh áº£nh Ä‘áº§u vÃ o : chá»©ng minh nhÃ¢n dÃ¢n (Loáº¡i má»›i) </h4>
            )
        }
        else {
            return (
                <h4>HÃ¬nh áº£nh Ä‘áº§u vÃ o :  CÄƒn cÆ°á»›c cÃ´ng dÃ¢n</h4>
            )
        }
    }

    tensorflowtinhtoan = () => {
        let names = [];
        let sexs = [];
        let sexname = ["NAM", "Ná»®"];

        dl.map((item) => {
            names.push((((item.namecat).charCodeAt(0) + (item.namecat).charCodeAt(1) + (item.namecat).charCodeAt(2)) - 169) / 77);
            sexs.push(sexname.indexOf(item.sexcat));
            return 0;
        })
        console.log(names);


        // dua data ve tensor
        let xs = tf.tensor1d(names);
        let convetys = tf.tensor1d(sexs, 'int32');
        let ys = tf.oneHot(convetys, 2);
        console.log(sexs);
        console.log(ys);

        // dua ve model va cac mo hinh

        let model = tf.sequential();
        let HiddenLayOut = tf.layers.dense({
            units: 16,
            activation: 'sigmoid',
            inputDim: 1,
        })
        let OutPutLayOut = tf.layers.dense({
            activation: 'softmax',
            units: 2
        })

        model.add(HiddenLayOut);
        model.add(OutPutLayOut);
        model.compile({
            optimizer: tf.train.sgd(0, 2),
            loss: 'categoricalCrossentropy'
        })

        console.log("Äang tá»± há»c");


        // train data
        async function train() {
            let option =
            {
                epochs: 200,
                validationSplit: 0.1,
                shuffle: true
            }
            return await model.fit(xs, ys, option);

        }

        train().then((res) => {
            console.log("há»c há»i thÃ nh cÃ´ng");
            console.log(res.history.loss);

            let data = this.props.dulieuchungminhnhdandan.name;
            let cut = data.slice(-3);
            function to_slug(str) {
                // Chuyá»ƒn háº¿t sang chá»¯ thÆ°á»ng
                str = str.toLowerCase();

                // xÃ³a dáº¥u
                str = str.replace(/(Ã |Ã¡|áº¡|áº£|Ã£|Ã¢|áº§|áº¥|áº­|áº©|áº«|Äƒ|áº±|áº¯|áº·|áº³|áºµ)/g, 'a');
                str = str.replace(/(Ã¨|Ã©|áº¹|áº»|áº½|Ãª|á»|áº¿|á»‡|á»ƒ|á»…)/g, 'e');
                str = str.replace(/(Ã¬|Ã­|á»‹|á»‰|Ä©)/g, 'i');
                str = str.replace(/(Ã²|Ã³|á»|á»|Ãµ|Ã´|á»“|á»‘|á»™|á»•|á»—|Æ¡|á»|á»›|á»£|á»Ÿ|á»¡)/g, 'o');
                str = str.replace(/(Ã¹|Ãº|á»¥|á»§|Å©|Æ°|á»«|á»©|á»±|á»­|á»¯)/g, 'u');
                str = str.replace(/(á»³|Ã½|á»µ|á»·|á»¹)/g, 'y');
                str = str.replace(/(Ä‘)/g, 'd');

                // XÃ³a kÃ½ tá»± Ä‘áº·c biá»‡t
                str = str.replace(/([^0-9a-z-\s])/g, '');

                // XÃ³a khoáº£ng tráº¯ng thay báº±ng kÃ½ tá»± -
                str = str.replace(/(\s+)/g, '');

                // xÃ³a pháº§n dá»± - á»Ÿ Ä‘áº§u
                str = str.replace(/^-+/g, '');

                // xÃ³a pháº§n dÆ° - á»Ÿ cuá»‘i
                str = str.replace(/-+$/g, '');

                // return
                return str;
            }
            let all = to_slug(cut);
            let ket = all.toUpperCase();

            // ket qua train
            let dataconvetint32 = (((ket.charCodeAt(0) + ket.charCodeAt(1) + ket.charCodeAt(2)) - 169) / 77);
            let tensordata = tf.tensor1d([dataconvetint32]);
            let aws = model.predict(tensordata);
            let max = aws.argMax(1).dataSync()[0];
            let ketqua = sexname[max];
            this.setState({
                ketquachinhthuc: ketqua
            });

            // this.props.setTrueGender(this.state.ketquachinhthuc);
            console.log(this.state.ketquachinhthuc);
        })
    }



    phongdoangioitinh = () => {
        if (this.props.dulieuchungminhnhdandan.sex === "N/A") {
            return (
                <button type="button" className="btn btn-success" onClick={() => this.tensorflowtinhtoan()}>Tá»° Há»ŒC</button>
            );
        }
        else{
            return (this.props.dulieuchungminhnhdandan.sex);
        }
    }

    render() {

        return (
            <>
            <div id="ket-qua-trich-xuat">
                {this.showconten()}
                <div id="anh-cmnd">
                    {this.props.dulieuchungminhnhdandan.cropped_idcard && <img src={this.props.dulieuchungminhnhdandan.cropped_idcard} alt="demo" />}
                    {this.props.dulieuchungminhnhdandan && <img className="card-img-top" width="120px" src={this.props.dulieuchungminhnhdandan} alt="" />}
                </div>
                <div id="thong-tin-khach">
                    <div id="avatar">
                        {this.props.dulieuchungminhnhdandan.face && <img width="220px" src={this.props.dulieuchungminhnhdandan.face} alt="demo2" />}
                    </div>
                    <div id="thong-tin-lay-duoc">
                        {this.props.dulieuchungminhnhdandan.name && <h5 className="card-title color_mana mb-10" style={{ marginBottom: '5px' }}>TÃªn : {this.props.dulieuchungminhnhdandan.name}</h5>}
                        {this.props.dulieuchungminhnhdandan.home && <h5 className="card-title color_mana mb-10" style={{ marginBottom: '5px' }}>Äá»‹a chá»‰ Ä‘Äƒng kÃ½ : {this.props.dulieuchungminhnhdandan.home}</h5>}
                        {this.props.dulieuchungminhnhdandan.id && <h5 className="card-title color_mana mb-10" style={{ marginBottom: '5px' }}>Sá»‘ cmnd : {this.props.dulieuchungminhnhdandan.id}</h5>}
                        {this.props.dulieuchungminhnhdandan.dob && <h5 className="card-title color_mana mb-10" style={{ marginBottom: '5px' }}>NgÃ y sinh : {this.props.dulieuchungminhnhdandan.dob}</h5>}
                        {this.props.dulieuchungminhnhdandan.sex && <h5 className="card-title color_mana  mb-10" style={{ marginBottom: '5px' }}>Giá»›i tÃ­nh : {this.phongdoangioitinh()}     {this.state.ketquachinhthuc}</h5>}
                    </div>
                </div>
            </div>
            <div className='clearfix' style={{display:'table',content:'',clear:'both'}}></div>
            </>
        );
    }
}

export default Viewdata;